import React, { PureComponent } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { LoginWrapper, LoginBox, Input,Button } from './style';
import { actionCreators } from '../store';

class Login extends PureComponent {
	constructor(props){
		super(props);
		this.state={
			account:"",
			password:""
		}
	}
	handleChangeAccount(e){
        this.setState({
			account:e.target.value
		})

	}
	handleChangePwd(e){
		this.setState({
			password:e.target.value
		})
	}
	render() {
		const { loginState,login } = this.props;
		console.log(login);
		if (!loginState) {
			return (
				<LoginWrapper>
					<LoginBox>
						<Input placeholder='账号' name="account" onChange={this.handleChangeAccount.bind(this)} value={this.state.account}/>
						{console.log(this.state.account)}
						<Input placeholder='密码' name='password' type="password" onChange={this.handleChangePwd.bind(this)} value={this.state.password}/>
						<Button onClick={() => this.props.login(this.state.account, this.state.password)}>登陆</Button>
					</LoginBox>
				</LoginWrapper>
			)
		}else {
			return <Redirect to='/'/>
		}
	}
}

const mapState = (state) => ({
	loginState: state.getIn(['common', 'login'])
})

const mapDispatch = (dispatch) => ({
	login(accountElem, passwordElem){
		console.log(accountElem);
		console.log(passwordElem)
		dispatch(actionCreators.login(accountElem.value, passwordElem.value))
	}
})

export default connect(mapState, mapDispatch)(Login);